import React, { useState } from "react";

import "./monitor.style.css";
export default function ({ players }) {
  const [currentpageNum, setPageNum] = useState(0);
  const setStatus = (address) => {
    document.socket.emit("set-status", address);
  };

  const preventPage = () => {
    setPageNum(currentpageNum--);
  };

  const nextPage = () => {
    setPageNum(currentpageNum++);
  };

  const getnavigation = () => {
    var i = players.length / 10;
    // i = i < 1 ? 1 : i;
    var map = [];
    map.push(
      <button disabled={currentpageNum === 0} onClick={preventPage} key={0}>
        {"<"}
      </button>
    );
    // for (var j = 0; j < i; j++) {
    map.push(
      <button disabled={true} key={1}>
        {currentpageNum + 1}
      </button>
    );
    // }
    map.push(
      <button
        disabled={currentpageNum >= Math.floor(i)}
        key={2}
        onClick={nextPage}
      >
        {">"}
      </button>
    );
    return map;
  };

  return (
    <div className="m-table">
      <div className="m-table-header">
        <div className="m-table-row">
          <div className="m-table-cell w-1">#</div>
          <div className="m-table-cell w-2">address</div>
          <div className="m-table-cell w-2">cookies</div>
          <div className="m-table-cell w-1">clicks/1m</div>
          <div className="m-table-cell w-2">status</div>
        </div>
      </div>
      <div className="m-table-body">
        {players.map((player, i) => {
          return (
            <div className="m-table-row" key={i}>
              <div className="m-table-cell w-1">
                {i + 1 + currentpageNum * 10}
              </div>
              <div className="m-table-cell w-2">
                <div
                  className="tooltip"
                  style={{ color: "green", cursor: "copy" }}
                  onCopy={() => {}}
                >
                  {player.address.substring(0, 4) +
                    "..." +
                    player.address.substring(
                      player.address.length - 4,
                      player.address.length
                    )}
                  <span className="tooltiptext">{player.address}</span>
                </div>
              </div>
              <div className="m-table-cell w-2">{player.cookies}</div>
              <div className="m-table-cell w-1">{player.aminclickes}</div>
              <div className="m-table-cell w-2">
                <label className="switch">
                  <input
                    type="checkbox"
                    checked={player.status}
                    onChange={() => setStatus(player.address)}
                  />
                  <span className="slider round"></span>
                </label>
              </div>
            </div>
          );
        })}
      </div>
      <div className="m-table-body">{getnavigation()}</div>
    </div>
  );
}
